.education {
    display: none;
    margin: 25px 0 25px 0;
}

.education-text {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.education-text__center {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
}

.education-font__colorBalck {
    color: #0f1521;
    text-align: center;
}

.education-font__colorGray {
    color: #56657D;
    text-align: center;
    margin: 3px 0;
}

.education-lineElement {
    height: 2px;
    width: 200px;
    background-color: #56657D;
}