.footer {
    width: 100%;
    height: 40px;
    margin: 0;
    padding: 0;
    z-index: 1;
    background-color: white;
    border-top: 2px solid #56657D;
    position: sticky;
    bottom: 0;
    left: 0;
}

.footer-info {
    text-align: center;
    margin: 10px 0 0 0;
    color: #56657D;
}