@keyframes mouseEnter {
    0% {
        backdrop-filter: blur(0px);
    }
    100% {
        backdrop-filter: blur(5px);
    }
}

@keyframes mouseLeave {
    0% {
        backdrop-filter: blur(5px);
    }
    100% {
        backdrop-filter: blur(0px);
    }
}

.header-picture {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    width: 100%;
    position: relative;
}

@media screen and (min-width:1192px) {
    .header-pictureItem {
        width: 400px;
        height: 400px;
        position: relative;
    }
    .header-pictureBackground {
        width: 380px;
        height: 400px;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.4;
        background-color: black;
    }
    .header-pictureItem__h2 {
        font-size: 36px;
    }
    .header-pictureItem__h1 {
        font-size: 50px;
    }
    .header-pictureItem__p {
        font-size: 24px;
    }
}

@media screen and (min-width:576px) and (max-width:1192px) {
    .header-pictureItem {
        width: 300px;
        height: 300px;
        position: relative;
    }
    .header-pictureBackground {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.4;
        background-color: black;
    }
}    

@media screen and (max-width:576px) {
    .header-pictureItem {
        width: 280px;
        height: 280px;
        position: relative;
    }
    .header-pictureBackground {
        width: 102%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.4;
        background-color: black;
    }
}

.header-pictureText {
    width: 80%;
    height: 100%;
    padding-left: 60px;
    position: absolute;
    top: -10px;
    left: -10px;
    opacity: 1;
    z-index: 2;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    border: solid 10px white;
}

.header-pictureItem__textColor {
    color: white;
}

.header-blurEnter {
    animation-name: mouseEnter;
    animation-duration: .5s;
    backdrop-filter: blur(5px);
}

.header-blurLeave {
    animation-name: mouseLeave;
    animation-duration: .5s;
    backdrop-filter: blur(0px);
}